/* this file goes into the Card folder because it only impacts the component Card */

.card-component
{
    padding: 20px;
    cursor: pointer;
}

.card-component img
{
    width: 100%;
    height: 200px;
    border: solid 10px #ffff;
    border-radius: 10px;
    box-shadow: 0px 0px 2px #000000;

}

.card-component:hover img
{
    box-shadow: 0px 0px 5px #000000;
}